<template lang="html">
  <!--此页面需要-->
  <Card style="height:1000px;">
    <div class="container">
      <div style="margin-left: 0px;">
        <Tabs type="card">
          <TabPane style="margin-left: 0px" label="全部">
            <div class="contentMain">
              <div class="contentLeft">
                <ul class="cont-ul" style="margin-left:80px;text-align: left" >
                  <movies-list v-for="item in movieItems" :key="item._id" :id="item._id" :movieName="item.movieName"
                               :movieImg="item.movieImg" :movieNumSuppose="item.movieNumSuppose"></movies-list>
                  <div style="background-color: #DAEBE3;text-align: center">
                    <Page :current="index" :total="totalNum1" :page-size="pageSize" show-elevator  @on-change="changePage"/>
                    <div style="height:20px "></div>
                  </div>
                  <!--引入MovieList-->
                </ul>
              </div>
            </div>
          </TabPane>
          <TabPane label="动漫">
            <div class="contentMain">
              <div class="contentLeft">
                <ul class="cont-ul"  style="margin-left:80px;text-align: left" >
                  <movies-list v-for="item in movieItems1" :key="item._id" :id="item._id" :movieName="item.movieName"
                               :movieImg="item.movieImg" :movieNumSuppose="item.movieNumSuppose"
                               :movieTag="item.movieTag"></movies-list>
                  <div style="background-color: #DAEBE3;text-align: center">
                    <Page :current="index" :total="totalNum2" :page-size="pageSize" show-elevator  @on-change="changePage"/>
                    <div style="height:20px "></div>
                  </div>
                  <!--引入MovieList-->
                </ul>
              </div>
            </div>
          </TabPane>
          <TabPane label="冒险">
<!--            style="margin-left:80px"-->
            <div class="contentMain">
              <div class="contentLeft">
            <ul class="cont-ul"  style="margin-left:80px;text-align: left" >
              <movies-list v-for="item in movieItems0" :key="item._id" :id="item._id" :movieName="item.movieName"
                           :movieImg="item.movieImg" :movieNumSuppose="item.movieNumSuppose"
                           :movieTag="item.movieTag"></movies-list>
              <div style="background-color: #DAEBE3;text-align: center;margin-bottom: 0px">
                <Page :current="index" :total="totalNum3" :page-size="pageSize" show-elevator  @on-change="changePage"/>
                <div style="height:20px "></div>
              </div>
            </ul>
            </div>
            </div>
          </TabPane>
          <TabPane label="剧情">
            <!--            style="margin-left:80px"-->
            <div class="contentMain">
              <div class="contentLeft">
                <ul class="cont-ul"  style="margin-left:80px;text-align: left" >
                  <movies-list v-for="item in movieItems4" :key="item._id" :id="item._id" :movieName="item.movieName"
                               :movieImg="item.movieImg" :movieNumSuppose="item.movieNumSuppose"
                               :movieTag="item.movieTag"></movies-list>
                  <div style="background-color: #DAEBE3;text-align: center;margin-bottom: 0px;padding-top: 50px">
                    <Page :current="index" :total="totalNum4" :page-size="pageSize" show-elevator  @on-change="changePage"/>
                    <div style="height:20px "></div>
                  </div>
                </ul>
              </div>
            </div>
          </TabPane>
        </Tabs>
      </div>
      <div>
        <movie-index-header></movie-index-header>   <!--  展示引入的header组件 -->
      </div>

    </div>

  </Card>
</template>
<script>
import MovieIndexHeader from '../components/MovieIndexHeader'
import CommonFooter from '../components/commonFooter'
import MoviesList from '../components/MoviesList'

export default {
  name: 'movieList',
  data() {
    return {
      movieItems: [],
      movieItems1: [],//动画
      movieItems0: [],//冒险,
      movieItems4: [],//剧情
      totalNum1:1,
      totalNum2:1,
      totalNum3:1,
      totalNum4:1,
      index:1,
      pageSize:8,
    }
  },
  components: {
    MovieIndexHeader,
    MoviesList
  },
  mounted() {
    document.querySelector('body').setAttribute('style', 'background-color:white')
  },
  beforeDestroy() {
    document.querySelector('body').removeAttribute('style')
  },
  //  这里用于获取数据
  created:function(index) {
    //    获得评论
    this.init(index);
    console.log("这里测试数据长度："+this.totalNum1)
  },
  methods:{
    init:function (index){
      //    获取分页电影
      this.$http.post('http://localhost:3000/movie/pageMovie',{start:index,limit:this.pageSize}).then((data) => {
        this.movieItems = data.body.data
        this.totalNum1 = data.body.totalNum //全部
        console.log("这里测试分页："+data.body.totalNum)
      });
      this.$http.post('http://localhost:3000/showTag', {start:index,limit:this.pageSize,movieTag: "1"}).then((data) => {
        this.movieItems1 = data.body.data
        this.totalNum2 = data.body.totalNum //动漫
        console.log(data.body)
      });
      this.$http.post('http://localhost:3000/showTag', {start:index,limit:this.pageSize,movieTag: "0"}).then((data) => {
        this.movieItems0 = data.body.data
        this.totalNum3 =  data.body.totalNum //冒险
        console.log(data.body)
      });
      this.$http.post('http://localhost:3000/showTag', {start:index,limit:this.pageSize,movieTag: "2"}).then((data) => {
        this.movieItems4 = data.body.data
        this.totalNum4 =  data.body.totalNum //剧情
        console.log(data.body)
      })
    },
    changePage(index){
      this.init(index)
    },
  }
}
// methods: {
//   tag:function (event){
//     console.log("assasas");
//
//     })
//   }
// }
// }
</script>

<style lang="css" scoped>
.container {
  width: 100%;
  margin: 0 auto;
}

.contentMain{
  background-color: white;
  height: 100%
}
.contentLeft {
  background-color: #DAEBE3;
}
</style>



