<template>
  <div class="container">
    <div>
      <movie-index-header ></movie-index-header>
    </div>
    <div class="contentMain">
      <div>
        <div class="contentLeft">
<!--          <P style="font-family:微软雅黑;font-size: 16px;margin:20px">-->
<!--            搜索结果如下：-->
<!--          </P>-->
<!--          <el-alert-->
<!--            title="消息提示的文案"-->
<!--            type="info">-->
<!--          </el-alert>-->
          <div>
          <movies-list v-for="item in movieItem" v-bind:key="item._id" v-bind:id="item._id" v-bind:movieName="item.movieName" :movieNumSuppose="item.movieNumSuppose"
                       v-bind:movieImg="item.movieImg"></movies-list>
          </div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>

import MovieIndexHeader from "../components/MovieIndexHeader";
import MoviesList from "../components/MoviesList";

let movieName='';

export default {
  name: "MovieNameList",
  data () {
    return {
      movieItems:[],
      movieItem:[],
      // route:''
    }
  },
  components: {
    MovieIndexHeader,
    MoviesList
  },
  // props: ['movieName'],
  methods:{
    init:function (){
      console.log(this.movieName);
      this.movieName = this.$route.query.movieName;
      console.log(this.$route.query.movieName);
      // console.log(m_name);
      // this.movieName = this.m_name;
      this.$http.post('http://localhost:3000/showName', {movieName: this.movieName}).then((data) => {
        this.movieItem = data.body.data;
      })
      this.info(true);
    },
    info (nodesc) {
      this.$Notice.open({
        title: '以下为搜索电影 “'+this.movieName+ '” 结果',
        desc: nodesc ? '' : 'Here is the notification description. Here is the notification description. '
      });
    }
  },
  created(){
    this.init();
  },
  watch:{
    // '$route':'created'
    "$route": function(to,from){
      this.init();
    }
  }

}
</script>

<style scoped>

</style>



