<template lang="html">
  <div>
    <div class="container">
      <movie-index-header></movie-index-header>
    </div>
    <div class="bbox"></div>
    <div class="box">
      <div style="width: 20%;padding-top: 8%">
        <h2 style="color: #88B091;font-size: 25px;margin-left: auto">Register</h2>
        <div class="box">
          <i-input size="large" type="text" v-model="username" placeholder="请输入用户名" autofocus>
            <Icon type="ios-person" slot="prefix"></Icon>
          </i-input>
        </div>
        <div class="box">
          <i-input size="large" type="password" v-model="password" placeholder="请输入密码">
            <Icon type="ios-lock" slot="prefix"></Icon>
          </i-input>
        </div>
        <div class="box">
          <i-input size="large" type="password" v-model="rePassword" placeholder="请再次输入密码">
            <Icon type="ios-lock" slot="prefix"></Icon>
          </i-input>
        </div>
        <div class="box">
          <i-input size="large" type="text" v-model="userMail" placeholder="请输入邮箱" autofocus>
            <Icon type="ios-mail" slot="prefix"></Icon>
          </i-input>
        </div>
        <div class="box">
          <i-input size="large" type="text" v-model="num" placeholder="请输入验证码" autofocus>
            <Icon type="ios-mail" slot="prefix"></Icon>
          </i-input>
          <i-button v-on:click=sendEmail()
                    style="margin-left: 10px;color:#88B091;background-color: white;border: #88B091 1px solid;width: 200px;height: 40px;font-size: larger">
            发送验证码
          </i-button>
        </div>
        <div class="box">
          <i-button type="success" v-on:click=userRegister()
                    style="margin-left: 10px;background-color: #88B091;border: #88B091;width: 200px;height: 40px;font-size: larger">
            注册
          </i-button>
          <!--      <button v-on:click=userRegister()>注册</button>-->
        </div>

      </div>
    </div>
  </div>
</template>

<script>

import MovieIndexHeader from '../components/MovieIndexHeader'

export default {
  name: 'registerPage',
  components: {MovieIndexHeader},
  data() {
    return {
      username: '',
      password: '',
      rePassword: '',
      userMail: '',
      userPhone: '',
      num: ''
    }
  },
  methods: {
    userRegister: function (event) {
      if (this.password !== this.rePassword) {
        this.$Modal.error({
          title: '两次密码不一致'
        })
        // alert('两次密码不一致')
      } else if (this.num.length == 0) {
        this.$Modal.error({
          title: '请输入验证码'
        })
      } else {
        let sendData = {
          username: this.username,
          password: this.password,
          userMail: this.userMail,
          userPhone: this.userPhone,
          userAvatar: 'https://dev-file.iviewui.com/userinfoPDvn9gKWYihR24SpgC319vXY8qniCqj4/avatar',
          num: this.num
        }
        this.$http.post('http://localhost:3000/users/register', sendData).then((registerData) => {
          if (registerData.body.status == 1) {
            this.$Modal.error({
              title: '注册失败',
              content: registerData.body.message
            })
            // alert(registerData.body.message)
          } else {
            this.$Modal.success({
              title: registerData.body.message
            })
            this.$router.go(-1)
          }
        })
      }
    },
    sendEmail: function (event) {
      if (this.userMail.length == 0) {
        this.$Modal.error({
          title: '请输入邮箱'
        })
      } else {
        let sendData = {
          userMail: this.userMail
        }
        this.$http.post('http://localhost:3000/users/mail', sendData).then((Data) => {
          if (Data.body.status == 1) {
            this.$Modal.success({
              title: "发送验证码失败"
            })
          } else {
            this.$Modal.success({
              title: "发送验证码成功，请查收邮件并填写验证码"
            })
          }
        })
      }

    }
  }
}
</script>

<style lang="css" scoped>
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.bbox {
  position: fixed;
  border: 2px solid #88B091;
  border-radius: 10px;
  top: 20%;
  left: 35%;
  width: 450px;
  height: 450px;
  background: white;
  z-index: -1;
}

i-input {
  line-height: 40px;
}
</style>



