<template lang="html">
<div>
<div class="comment">
  <h2 style="margin-bottom: 10px"> 评论区：</h2>
    <list v-for="item in items" :key="item.id" style="list-style: none">
      <ListItem>
        <ListItemMeta :avatar= "item.userAvatar" :title="item.username" :description="item.context" />
<!--        {{item.context}}-->
        <template slot="action" v-if="isAdmin">
          <li>
            <a style="text-decoration: underline" v-on:click="deleteComment(item._id)">删除</a>
          </li>
        </template>
      </ListItem>
<!--    {{ item.username }}评论：-->
  </list>
</div>
<!--  <template v-for="(item,index) in items">-->
  <Page :current="index" :total="totalNum" :page-size="pageSize" show-elevator style="margin:20px" @on-change="changePage"/>
<!--  </template>-->
<div style="padding: 5px">
  <Input v-model="context" show-word-limit type="textarea" placeholder="说点什么叭！..." style="width: 800px"  :rows="10"/>
<!--     <textarea v-model="context" style="width: 80%;height:50px ;" placeholder="内容"></textarea>-->
</div>
<div style="padding-top: 10px;margin-bottom: 20px">
  <Button type="success" ghost v-on:click="send_comment">评论</Button>
</div>
</div>

</template>

<script>

export default {
  props: ['movie_id'],
  data () {
    return {
      item:[],
      isAdmin:false,
      items: [],
      context:'',
      totalNum:1,
      index:1,
      pageSize:3,
      // userAvatar:"https://dev-file.iviewui.com/userinfoPDvn9gKWYihR24SpgC319vXY8qniCqj4/avatar"
      // username: item.username
      send_data:[],
      info:[]
    }
  },
  created:function(index) {
    //    获得评论
   this.init(index);
  },
  methods: {
    init:function (index){
      console.log(this.movie_id)
      this.$http.post('http://localhost:3000/movie/pagecomment', {start:index,limit:this.pageSize,movie_id: this.movie_id}).then((data) => {
        if (data.body.status == 0) {
          this.items = data.body.data;
          // if(this.items.userAvatar){
          //   this.userAvatar = this.items.userAvatar;
          // }else{
          //   this.userAvatar = "https://dev-file.iviewui.com/userinfoPDvn9gKWYihR24SpgC319vXY8qniCqj4/avatar";
          // }
          this.$http.post('http://localhost:3000/movie/totalNum', {movie_id: this.movie_id}).then((data) => {
            if (data.body.status == 0) {
              this.totalNum = data.body.data;
            } else {
              alert('获得失败')
            }
          });
          this.$http.post('http://localhost:3000/users/showUser', {user_id: sessionStorage.getItem('_id')}).then((getUserInfo) => {
            if (getUserInfo.body.status === 0) {
              this.info = getUserInfo.body.data[0]
              console.log(this.info.userAdmin)
              if (this.info.userAdmin) {
                this.isAdmin = true
              }

            } else {
              console.log(getUserInfo.body);
              // alert(userData.body.message)
              this.$Notice.info({title: getUserInfo.body.message});
            }
          })
        } else {
          this.$Notice.error({title: "获取数据失败"});
        }
      })
    },
    deleteComment:function(commentId){
      console.log(commentId)
      this.$http.post('http://localhost:3000/admin/delComment', {commentId:commentId,username:sessionStorage.getItem('username'),
        token:sessionStorage.getItem('token'),id:sessionStorage.getItem('_id')}).then((data) => {
          this.$Notice.info({title: data.body.message});
          this.init(this.index);
      })
    },
    changePage(index){
      this.init(index)
    },
    send_comment (event) { //改为用id
      if (typeof (sessionStorage.getItem('_id')) !== 'undefined') { //已登录
        let send_data = {
          movie_id: this.movie_id,
          context: this.context,
          username: this.info.username,
          userAvatar: this.info.userAvatar
        }
        // console.log(send_data)
        this.$http.post('http://localhost:3000/users/postComment', send_data).then((data) => {
          console.log(data.body)
          this.$Notice.success({title: "评论成功"});
          this.init(this.index);
        })
        this.context = ''
      } else {
         let send_data = {
          movie_id: this.movie_id,
          context: this.context
        }
        // console.log(send_data)
        this.$http.post('http://localhost:3000/users/postComment', send_data).then((data) => {
          console.log(data.body)
          this.$Notice.success({title: "评论成功"});
          this.init(this.index);
        })
      }

    }
  }
}
</script>
<style lang="css" scoped>
.comment{
  margin:auto;
  background-color: white;
  width: 800px;
  height:300px;
  border-radius: 10px;
  text-align: left;
  padding: 20px;
}
</style>



