<template lang="html">
  <div class="back">
    <div class="container">
      <movie-index-header></movie-index-header>
    </div>
    <div class="bbox"></div>
    <div class="box">
      <div style="width: 20%;padding-top: 8%">
        <div class="demo-avatar">
          <Avatar shape="square" style="background-color: #88B091" icon="ios-person" size="large"/>
        </div>
        <div class="box">
          <i-input type="text" v-model="username" size="large" placeholder="请输入用户名" autofocus>
            <Icon type="ios-person" slot="prefix"></Icon>
          </i-input>
        </div>
        <div class="box">
          <i-input type="password" v-model="password" size="large" placeholder="请输入密码">
            <Icon type="ios-lock" slot="prefix"></Icon>
          </i-input>
        </div>
      </div>
    </div>
    <div class="box">

      <i-button type="success" v-on:click=userRegister() size="large"
                style="margin-left: 10px;color: #88B091;background-color: white;border:#88B091 1px solid">注册</i-button>
      <i-button type="primary" v-on:click="userLogin" size="large" style="margin-left: 10px;">登 录</i-button>
      <i-button type="default" v-on:click.native="value1=true" size="large"
                style="color: #88B091;background-color: white;border: none;">忘记密码
      </i-button>
    </div>
    <div>
      <!--      <common-footer></common-footer>-->
    </div>
    <Drawer width="380px" title="修改密码：" :closable="false" v-model="value1">
      <div style="margin: 20px;">
        <Card>
          <Form :label-width="60">
            <FormItem label="用户名">
              <Input v-model="username" placeholder="请输入用户名..."></Input>
            </FormItem>
            <FormItem label="用户邮箱">
              <Input v-model="userMail" placeholder="请输入用户邮箱..."></Input>
            </FormItem>
            <FormItem label="新密码">
              <Input v-model="userNewPassword" type="password" placeholder="请输入新密码..."></Input>
            </FormItem>
            <FormItem label="确认密码">
              <Input v-model="userRePassword" type="password" placeholder="再次输入新密码..."></Input>
            </FormItem>
            <FormItem>
              <Button type="primary" v-on:click.native=findPassword()>重置密码</Button>
              <Button style="margin-left: 8px" v-on:click.native="value1 = false">取消</Button>
            </FormItem>
          </Form>
        </Card>
      </div>
    </Drawer>
  </div>
</template>

<script>
import MovieIndexHeader from '../components/MovieIndexHeader'
import CommonFooter from '../components/CommonFooter'

export default {
  name: 'loginPage',
  components: {CommonFooter, MovieIndexHeader},
  data() {
    return {
      // Find:false,
      username: '',
      password: '',
      userMail:'',
      userNewPassword:'',
      userRePassword:'',
      value1:false
    }
  },
  methods: {
    // 跳转到的登录页面
    userLogin: function (event) {
      this.$http.post('http://localhost:3000/users/login', {
        username: this.username,
        password: this.password
      }).then((loginData) => {
        if (loginData.body.status === 0) {
          let save_token = {
            token: loginData.body.data.token,
            username: this.username
          }
          sessionStorage.setItem('token', loginData.body.data.token)
          sessionStorage.setItem('username', this.username)
          sessionStorage.setItem('_id', loginData.body.data.user[0]._id)
          this.$Notice.success({title:'欢迎登录！'+ this.username});
          // sessionStorage.setItem('userAvatar', loginData.body.data.user[0].userAvatar)
          if (loginData.body.data.user[0].userAdmin) {
            // 若是管理员用户则跳转到后台页面
            this.$router.push({path: 'admin', query: {id: loginData.body.data.user[0]._id}})
          } else {
            this.$router.push({path: '/'})
          }
        } else {
          this.$Modal.error({
            title: '登录失败',
            content: loginData.body.message
          })
          // alert(loginData.body.message)
        }
      })
    },
    // 跳转到注册页面
    userRegister: function (event) {
      this.$router.push({path: 'registerPage'})
    },
    // 跳转到找回密码页面
    forgetPassword: function (event) {
      this.$router.push({path: 'forgetPassword'})
    },
    findPassword:function(event){
      this.$http.post('http://localhost:3000/users/findPassword', {username: this.username, userMail: this.userMail,
        userNewPassword: this.userNewPassword,userRePassword:this.userRePassword}).then((ChangeData) => {
        if (ChangeData.body.status === 0) {
          this.Find = false;
          this.$Modal.success({
            title: ChangeData.body.message
          })
        }else{
          this.$Modal.error({
            title: ChangeData.body.message})
        }
      })
    }
  }
}
</script>

<style lang="css" scoped>
.container {
  width: 100%;
  margin: 0 auto;
}

.box {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bbox {
  position: fixed;
  border: 2px solid #88B091;
  border-radius: 10px;
  top: 20%;
  left: 35%;
  width: 450px;
  height: 300px;
  background: white;
  z-index: -1;
}

.demo-avatar {
  padding-bottom: 20px;
}


</style>



