import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '../components/HelloWorld'
import VueResource from 'vue-resource'
import MovieList from '../pages/moviesList'
import MovieDetail from '../pages/movieDetail'
import LoginPage from '../pages/loginPage'
import RegisterPage from '../pages/registerPage'
import MovieNameList from "../pages/MovieNameList";
import Upload from '../components/uploadtest';
import userInfo from "../components/userInfo";
import changeInfo from "../pages/changeInfo";
import adminPage from "../components/adminPage"
import movieAdd from "../pages/movieAdd";
import adminUser from "../pages/adminUser";
import movieAdmin from "../pages/movieAdmin";
import recommendAdmin from "../pages/recommendAdmin";
import adminMovieDetail from "../pages/adminMovieDetail";
import RecommendList from "../pages/RecommendList";

Vue.use(Router)
Vue.use(VueResource)

const routes = [
  {
    path: '/',
    component: resolve => require(['../pages/index'], resolve),
    meta: {
      title: 'home'
    }
  },
  {
    path: '/movieList',
    component: MovieList
  },
  {
    path: '/movieDetail',
    component: MovieDetail
  },
  {
    path: '/loginPage',
    component: LoginPage
  },
  {
    path: '/registerPage',
    component: RegisterPage
  },
  {
    path: '/showName',
    component: MovieNameList,
    // props: true

  },
  {
    path: '/changeInfo',
    component: changeInfo
  },
  {
    path: '/upload',
    component:Upload
  },
  {
    path:'/admin',
    component:adminPage
  },
  {
    path:'/movieAdd',
    component:movieAdd
  },
  {
    path: '/adminUser',
    component: adminUser
  },
  {
    path: '/movieAdmin',
    component: movieAdmin
  },
  {
    path: '/recommendAdmin',
    component: recommendAdmin
  },
  {
    path: '/adminMovieDetail',
    component: adminMovieDetail
  },
  {
    path: '/RecommendList',
    component: RecommendList
  }

]
const originalReplace = Router.prototype.replace;
Router.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err);
};


const router = new Router({routes})

export default router



