<template>
  <div>
    <div>
      <movie-index-header></movie-index-header>
    </div>
    <div>
     <admin-page></admin-page>
    </div>
    <div>
      <Form ref="formItem" :rules="ruleValidate" :model="recommend" :label-width="80"
            style="text-align:left;margin:auto;width:500px;padding-top: 30px">
        <!--    <h2 style="text-align: center;padding: 10px">添加电影</h2>-->
        <FormItem label="电影名称" prop="recommendTitle">
          <Input v-model="recommend.recommendTitle" placeholder="请输入电影名称"></Input>
        </FormItem>
        <FormItem label="电影海报" prop="recommendImg">
         <img :src="recommend.recommendImg" v-if="recommend.recommendImg" width="400px" style="border-radius: 3px;margin-left: 10px">
          <Upload
            type="drag"
            style="margin-top: 10px;"
            :data="qiniuForm"
            :before-upload="beforeUpload"
            :on-success="uploadSuccess"
            :limit="1"
            action="https://up-z2.qiniup.com">
            <div style="padding: 20px 0">
              <Icon type="ios-cloud-upload" size="52" style="color:#88B091"></Icon>
              <p>上传海报</p>
            </div>
          </Upload>
          <Input v-model="recommend.recommendImg" placeholder="请输入电影海报地址"></Input>
        </FormItem>
        <FormItem label="电影资源" prop="recommendSrc">
          <Upload
            type="drag"
            style="margin-top: 10px;"
            :data="qiniuForm"
            :before-upload="beforeUploadMovie"
            :on-success="uploadSuccessMovie"
            accept=".mp4"
            :limit="1"
            action="https://up-z2.qiniup.com">
            <div style="padding: 20px 0">
              <Icon type="ios-cloud-upload" size="52" style="color:#88B091"></Icon>
              <p>上传电影</p>
            </div>
          </Upload>
          <Input v-model="recommend.recommendSrc" placeholder="请输入电影资源或者电影预告片"></Input>
        </FormItem>

        <FormItem style="text-align: center">
          <Button type="primary" @click="handleSubmit('formItem')">增加</Button>
          <Button @click="handleReset('formItem')" style="margin-left: 8px">清空</Button>
        </FormItem>
      </Form>

    </div>
  </div>
</template>

<script>
import MovieIndexHeader from "../components/MovieIndexHeader";
import AdminPage from "../components/adminPage";
export default {
name: "recommendAdmin",
  components: {AdminPage, MovieIndexHeader},
  data(){
  return{
    recommend:{
      recommendTitle:'',
      recommendImg:'',
      recommendSrc:'',
      recommend_id:''
    },
    domain: 'https://up-z2.qiniup.com',
    // 这是七牛云空间的外链默认域名
    qiniuaddr: 'qmaqpkabs.hn-bkt.clouddn.com',
    qiniuForm:{
      key:'',
      token:''
    },
    ruleValidate: {
      recommendTitle: [
        {required: true, message: '电影名称不能为空', trigger: 'blur'}
      ],
      recommendImg: [
        {required: true, message: '电影海报不能为空', trigger: 'blur'},
      ],

    },
  }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('校验成功!');
          let recommend_data;
          if (typeof (sessionStorage.getItem('username')) !== 'undefined') {
            recommend_data = {
              username: sessionStorage.getItem('username'),
              token: sessionStorage.getItem('token'),
              user_id: sessionStorage.getItem('_id'),
              recommendTitle:this.recommend.recommendTitle,
              recommendImg:this.recommend.recommendImg,
              recommendSrc:this.recommend.recommendSrc,
            }
            console.log(recommend_data)
            this.$http.post('http://localhost:3000/admin/addRecommend', {recommendData:recommend_data}).then((addData) => {
              if (addData.body.status === 0) {
                this.$Modal.success({
                  title: addData.body.message
                })
                this.handleReset('formItem')
              } else {
                this.$Modal.error({
                  title: addData.body.message
                })
              }
            })
          } else {
            this.$Modal.error({
              title: '请先登录'
            })
          }
        } else {
          this.$Message.error('Fail!');
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    changeMore() {
      this.formItem.more = true
    },
    async beforeUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      // const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG或PNG 格式!')
      }

      //如果验证失败，返回假
      if(isJPG == false) return false;

      //如果验证成功，获取token
      let fileType = file.type == 'image/jpeg' ? 'jpg' : 'png'; //获取扩展名
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key:'recommendImg_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
        token: res.data.data.token  // 根据自己接口打印的值来赋值
      }
      this.$Notice.info({title:'正在上传图片'});
      return true;
    },

    /**
     * 上传成功之后得到image
     * @param res
     * @param file
     * @param fileList
     */
    uploadSuccess(res,file,fileList) {
      //console.log(res)
      this.recommend.recommendImg='http://qmaqpkabs.hn-bkt.clouddn.com/'+res.key
      this.$Notice.info({title:'上传完成'});
      // let url=this.imageUrl
    },
    async beforeUploadMovie(file) {
      console.log("ssssccccc")
      const isMP4 = file.type === 'video/mp4'
        // || file.type === 'image/png'
      // const isLt2M = file.size / 1024 / 1024 < 2

      if (!isMP4) {
        this.$message.error('上传视频暂只支持 MP4 格式!')
      }

      //如果验证失败，返回假
      if(isMP4 == false) return false;

      //如果验证成功，获取token
      let fileType = 'mp4'; //获取扩展名
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key:'recommendSrc_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
        token: res.data.data.token  // 根据自己接口打印的值来赋值
      }
      this.$Notice.info({title:'正在上传图片'});
      return true;
    },

    /**
     * 上传成功之后得到image
     * @param res
     * @param file
     * @param fileList
     */
    uploadSuccessMovie(res,file,fileList) {
      //console.log(res)
      this.recommend.recommendSrc='http://qmaqpkabs.hn-bkt.clouddn.com/'+res.key
      this.$Notice.info({title:'上传完成'});
      // let url=this.imageUrl
    }
  }
}
</script>

<style scoped>

</style>



