<template>
  <div>
    <div>
      <movie-index-header></movie-index-header>
    </div>
    <div>
      <admin-page></admin-page>
    </div>
    <div style="margin:30px 20px 20px 150px;padding-top: 30px">
      <i-col span="100" offset="1">
        <Table :loading="loadFlag" :columns="columns" :data="RecommendData" height="700" size="large" ref="table"
               :border=true>
          <template slot-scope="{ row, index }" slot="recommendTitle">
            <Input type="text" v-model="editRecommendTitle" v-if="editIndex === index"/>
            <!--              <span v-else>{{ row.movieName }}</span>-->
            <span
              v-else>{{
                row.recommendTitle.length <= 18 ? row.recommendTitle : row.recommendTitle.substring(0, 18) + '...'
              }}</span>
          </template>

          <template slot-scope="{ row, index }" slot="recommendImg">
            <div v-if="editIndex === index" style="padding: 10px">

              <Input type="text" v-model="editRecommendImg"/>
              <Upload
                type="drag"
                style="margin-top: 10px"
                :data="qiniuForm"
                :before-upload="beforeUpload"
                :on-success="uploadSuccess"
                :limit="1"
                action="https://up-z2.qiniup.com">
                <Button icon="ios-cloud-upload-outline">Upload files</Button>
              </Upload>
            </div>

            <!--              <span v-else>{{ row.movieName }}</span>-->
            <div v-else>
              <img :src="row.recommendImg" style="width: 300px">
              <p>{{ row.recommendImg.length <= 18 ? row.recommendImg : row.recommendImg.substring(0, 50) + '...' }}</p>
            </div>
          </template>
          <template slot-scope="{ row, index }" slot="recommendSrc">
            <div v-if="editIndex === index">
              <Input type="text" v-model="editRecommendSrc"/>
              <Upload
              type="drag"
              style="margin-top: 10px"
              :data="qiniuForm"
              :before-upload="beforeUploadMovie"
              :on-success="uploadSuccessMovie"
              :limit="1"
              action="https://up-z2.qiniup.com">
              <Button icon="ios-cloud-upload-outline">Upload files</Button>
            </Upload>

            </div>

            <!--              <span v-else>{{ row.movieName }}</span>-->
            <div v-else>
              <!--              <img :src="row.recommendSrc" style="width: 300px">-->
              <span>{{
                  row.recommendSrc.length <= 18 ? row.recommendSrc : row.recommendSrc.substring(0, 30) + '...'
                }}</span>
            </div>

          </template>
          <template slot-scope="{ row, index }" slot="action">
            <div v-if="editIndex === index">
              <Button @click="handleSave(index)" style="color: green;">保存</Button>
              <Button @click="editIndex = -1" style="color: red;">取消</Button>
            </div>
            <div v-else>
              <Button @click="handleEdit(row, index)">编辑</Button>
            </div>
          </template>

        </Table>
      </i-col>
    </div>

  </div>
</template>

<script>
import MovieIndexHeader from "../components/MovieIndexHeader";
import AdminPage from "../components/adminPage";

export default {
  name: "RecommendList",
  components: {AdminPage, MovieIndexHeader},
  data() {
    return {
      modal9: false,
      loadFlag: true,
      domain: 'https://up-z2.qiniup.com',
      // 这是七牛云空间的外链默认域名
      qiniuaddr: 'qmaqpkabs.hn-bkt.clouddn.com',
      qiniuForm: {
        key: '',
        token: ''
      },
      columns: [
        {
          title: '电影名称',
          slot: 'recommendTitle',
          align: 'center'
        },
        {
          title: '电影海报',
          slot: 'recommendImg',
          align: 'center',
          width: '400px'
        },
        {
          title: '电影资源',
          slot: 'recommendSrc',
          align: 'center',
        },
        {
          title: '编辑',
          slot: 'action',
          // width: 160,
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click:() => {
                    this.show(params.index)
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'error'
                  // size: 'small'
                },
                on: {
                  click: () => {
                    this.remove(params.index)
                  }
                }
              }, '删除'),
            ])
          }
        }
      ],
      RecommendData: [],
      editRecommendTitle: '',
      editRecommendSrc: '',
      editRecommendImg: '',
      editIndex: '',
      modal:{
        showDetail:false
      },
      recommendDetail: '',
    }
  },
  created() {
    this.$http.get('http://localhost:3000/showIndex').then((data) => {
      this.RecommendData = data.body.data
      this.loadFlag = false
      console.log(data.body.data)
    })
  },
  methods: {
    show(index) {
      // this.recommendDetail = ` 电影ID：${this.RecommendData[index]._id}\n\n 电影名称：${this.RecommendData[index].recommendTitle}\n\n 电影海报地址：${this.RecommendData[index].recommendImg}\n\n 电影资源地址：${this.RecommendData[index].recommendSrc}`
      this.recommendDetail = ` 电影ID：${this.RecommendData[index]._id}<br> 电影名称：${this.RecommendData[index].recommendTitle}<br> 电影海报地址：${this.RecommendData[index].recommendImg}<br> 电影资源地址：${this.RecommendData[index].recommendSrc}`

      this.$Modal.info({
        title: '电影信息',
        width: 900,
        top: 300,
        scrollable:true,
        content: this.recommendDetail
      })
    },
    remove(index) {
      let send_data;
      if (typeof (sessionStorage.username) !== 'undefined') {
        send_data = {
          username: sessionStorage.username,
          token: sessionStorage.token,
          user_id: sessionStorage._id,
          recommend_id: this.RecommendData[index]._id
        }
        this.$Modal.confirm({
          title: '确认删除此条数据吗？',
          onOk: () => {
            this.$http.post('http://localhost:3000/admin/delRecommend', send_data).then((delData) => {
              if (delData.body.status === 0) {
                this.$Notice.success({
                  title: delData.body.message,
                  duration: 3,
                  top: 150
                })
              } else {
                this.$Modal.error({
                  title: delData.body.message
                })
              }
            })
            // 从页面删除电影
            this.RecommendData.splice(index, 1)
          },
          onCancel: () => {
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }

    },
    handleEdit(row, index) {
      this.editIndex = index
      this.editRecommendImg = row.recommendImg
      this.editRecommendSrc = row.recommendSrc
      this.editRecommendTitle = row.recommendTitle
    },
    handleSave(index) {
      let update_data;
      if (typeof (sessionStorage.username) !== 'undefined') {
        update_data = {
          username: sessionStorage.username,
          token: sessionStorage.token,
          user_id: sessionStorage._id,
          recommend_id: this.RecommendData[index]._id,
          recommendTitle: this.editRecommendTitle,
          recommendSrc: this.editRecommendSrc,
          recommendImg: this.editRecommendImg,
        }
        console.log(update_data)
        this.$http.post('http://localhost:3000/admin/recommendUpdate', {recommendData:update_data}).then((updateData) => {
          if (updateData.body.status === 0) {
            this.$Notice.success({
              title: updateData.body.message,
              duration: 3,
              top: 150
            })
            // this.$Modal.success({
            //   title: updateData.body.message
            // })
            // 从页面修改电影
            this.RecommendData[index].recommendTitle = this.editRecommendTitle
            this.RecommendData[index].recommendImg = this.editRecommendImg
            this.RecommendData[index].recommendSrc = this.editRecommendSrc
            this.editIndex = -1

          } else {
            this.$Modal.error({
              title: updateData.body.message
            })
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    },
    async beforeUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      // const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG或PNG 格式!')
      }

      //如果验证失败，返回假
      if (isJPG == false) return false;

      //如果验证成功，获取token
      let fileType = file.type == 'image/jpeg' ? 'jpg' : 'png'; //获取扩展名
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key: 'movieImg_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
        token: res.data.data.token  // 根据自己接口打印的值来赋值
      }
      this.$Notice.info({title: '正在上传图片'});
      return true;
    },

    /**
     * 上传成功之后得到image
     * @param res
     * @param file
     * @param fileList
     */
    uploadSuccess(res, file, fileList) {
      //console.log(res)
      this.editRecommendImg = 'http://qmaqpkabs.hn-bkt.clouddn.com/' + res.key
      this.$Notice.info({title: '上传完成'});
      // let url=this.imageUrl
      console.log(this.RecommendData)
      console.log(this.editRecommendTitle)
      // this.RecommendData.recommendTitle = this.editRecommendTitle
      // this.RecommendData.recommendImg =
      // this.RecommendData.recommendSrc = this.editRecommendSrc
    },
  async beforeUploadMovie(file) {
    console.log("ssssccccc")
    const isMP4 = file.type === 'video/mp4'
    // || file.type === 'image/png'
    // const isLt2M = file.size / 1024 / 1024 < 2

    if (!isMP4) {
      this.$message.error('上传视频暂只支持 MP4 格式!')
    }

    //如果验证失败，返回假
    if(isMP4 == false) return false;

    //如果验证成功，获取token
    let fileType = 'mp4'; //获取扩展名
    let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
    console.log(res);
    console.log(res.data.data.token)
    // return;
    this.qiniuForm = {
      key:'recommendSrc_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
      token: res.data.data.token  // 根据自己接口打印的值来赋值
    }
    this.$Notice.info({title:'正在上传图片'});
    return true;
  },

  /**
   * 上传成功之后得到image
   * @param res
   * @param file
   * @param fileList
   */
  uploadSuccessMovie(res,file,fileList) {
    //console.log(res)
    this.this.editRecommendSrc='http://qmaqpkabs.hn-bkt.clouddn.com/'+res.key
    this.$Notice.info({title:'上传完成'});
    // let url=this.imageUrl
  }
  }

}
</script>

<style scoped lang="css">

.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ivu-modal{
  top: 0;
}
</style>



