<template style="background-color: #DAEBE3">

  <div style="text-align: center;margin: auto;padding: 10px;">
    <!--    border: #88B091 2px solid;width: 300px;border-radius: 10px-->
    <Card style="width: 300px;height:200px;margin: auto;" bordered>
      <table align="center">
        <tr class="row">
          <td colspan="2">
            <Img :src="info.userAvatar" style="width: 50px;border-radius: 4px;border: #88B091 solid 2px"/>
          </td>
        </tr>
        <div style="margin: 10px">
          <tr class="row">
            <td style="text-align: right;margin: 10px">用户名：</td>
            <td style="text-align: left">{{ info.username }}</td>
          </tr>
          <tr class="row">
            <td>用户邮箱：</td>
            <td style="text-align: left">{{ info.userMail }}</td>
          </tr>
          <tr class="row">
            <td>用户身份：</td>
            <td style="text-align: left">{{ info.admin }}</td>
          </tr>
          <tr class="row">
            <td>用户状态：</td>
            <td style="text-align: left">{{ info.userStatus }}</td>
          </tr>
        </div>

      </table>
    </Card>
    <div style="text-align: right;margin: 30px">
      <Button shape="circle" v-on:click.native="Find = true">修改密码</Button>
    </div>
    <div style="margin: 20px;">
      <Card v-if="Find">
        <Form :label-width="60">
          <FormItem label="用户名">
            <Input v-model="username" placeholder="请输入用户名..."></Input>
          </FormItem>
          <FormItem label="用户邮箱">
            <Input v-model="userMail" placeholder="请输入用户邮箱..."></Input>
          </FormItem>
          <FormItem label="新密码">
            <Input v-model="userNewPassword" type="password" placeholder="请输入新密码..."></Input>
          </FormItem>
          <FormItem label="确认密码">
            <Input v-model="userRePassword" type="password" placeholder="再次输入新密码..."></Input>
          </FormItem>
          <FormItem>
            <Button type="primary" v-on:click.native=findPassword()>重置密码</Button>
            <Button style="margin-left: 8px" v-on:click.native="Find = false">取消</Button>
          </FormItem>
        </Form>
      </Card>
    </div>
  </div>

</template>

<script>
import MovieIndexHeader from "./MovieIndexHeader";

export default {
  name: "userInfo",
  components: {MovieIndexHeader},
  data() {
    return {
      userId: '',
      info: [],
      Find: false,
      username: '',
      userMail: '',
      userNewPassword: '',
      userRePassword: ''
    }
  },
  props: ['id'],
  created() {
    let userId = this.id
    // console.log(userId + "ttt")
    if (userId) {
      this.$http.post('http://localhost:3000/users/showUser', {user_id: userId}).then((getUserInfo) => {
        if (getUserInfo.body.status === 0) {
          this.info = getUserInfo.body.data[0]
          console.log(getUserInfo.body.data[0].userStop)
          if (getUserInfo.body.data[0].userStop) {
            this.info.userStatus = '已被封停'
          } else {
            this.info.userStatus = '正常'
          }
          console.log(getUserInfo.body.data[0])
          console.log(getUserInfo.body.data[0].userAdmin)
          if (getUserInfo.body.data[0].userAdmin) {
            this.info.admin = '管理员用户'
          } else {
            this.info.admin = '普通用户'
          }
          console.log("测试用户身份" + getUserInfo.body.data[0].userAdmin)
        } else {
          console.log(getUserInfo.body);
          // alert(userData.body.message)
          this.$Notice.info({title: getUserInfo.body.message});
        }
      })
    } else {
      // this.$Notice.info({title:'请先登录再查看个人信息'});
      // alert('请先登录再查看个人信息')
    }

  },
  methods:{
    findPassword:function(event){
      this.$http.post('http://localhost:3000/users/findPassword', {username: this.username, userMail: this.userMail,
        userNewPassword: this.userNewPassword,userRePassword:this.userRePassword}).then((ChangeData) => {
        if (ChangeData.body.status === 0) {
          this.$Modal.success({
            title: ChangeData.body.message
          })
          this.Find = false
        }else{
          this.$Modal.error({
            title: ChangeData.body.message})
        }
      })
    }

  }
}
</script>

<style lang="css" scoped>
body {
  background-color: #DAEBE3
}

</style>



