<template>
<div>
  <div>
    <admin-page></admin-page>
  </div>
  <div style="margin:30px 20px 20px 200px;padding-top: 30px">
    <Table :loading="loadFlag" :columns="columns" :data="userData" height="700" size="large" ref="table" :border="true">
      <template slot-scope="{ row, index }" slot="userAvatar">
<!--        <span>{{ row.username.length <= 18 ? row.username : row.username.substring(0, 18) + '...'}}</span>-->
        <img :src="row.userAvatar" style="width: 50px">
      </template>
      <template slot-scope="{ row, index }" slot="username">
        <span>{{ row.username.length <= 18 ? row.username : row.username.substring(0, 18) + '...'}}</span>
      </template>

      <template slot-scope="{ row, index }" slot="password">
        <span>{{ row.password.length <= 30 ? row.password : row.password.substring(0, 25) + '...'}}</span>
      </template>

      <template slot-scope="{ row, index }" slot="userMail">
        <span>{{ row.userMail }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="userAdmin">
        <label>
        <Select v-model="editAdmin" style="width:95%" v-if="editIndex === index" >
          <Option v-for="item in adminList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <!--              <Input type="text" v-model="editAdmin" v-if="editIndex === index" />-->
        <span v-else>{{ row.userAdmin }}</span>
        </label>
      </template>

<!--      <template slot-scope="{ row, index }" slot="userPower">-->
<!--        <Select v-model="editPower" style="width:95%" v-if="editIndex === index" >-->
<!--          <Option v-for="item in powerList" :value="item.value" :key="item.value">{{ item.label }}</Option>-->
<!--        </Select>-->
<!--        &lt;!&ndash;              <Input type="text" v-model="editPower" />&ndash;&gt;-->
<!--        <span v-else>{{ row.userPower }}</span>-->
<!--      </template>-->

      <template slot-scope="{ row, index }" slot="userStop">
        <span>{{ row.userStop }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="stop">
        <div>
          <Button  :key="1" v-show="row.userStop == false" @click="handleStop(row, index)" type="error" ghost>封停</Button>
          <Button :key="2" v-show="row.userStop == true" @click="handleCancelStop(row, index)" type="success" ghost>解封</Button>
        </div>
      </template>

      <template slot-scope="{ row, index }" slot="action">
        <div v-if="editIndex === index">
          <Button @click="handleSave(index)" type="success" ghost>保存</Button>
          <Button @click="editIndex = -1" type="info" ghost>取消</Button>
        </div>
        <div v-else>
          <Button @click="handleEdit(row, index)">编辑</Button>
        </div>
      </template>
<!--      <template slot-scope="{ row, index }" slot="action">-->
<!--        <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">详细信息</Button>-->
<!--&lt;!&ndash;        <Switch v-model="switch1" @on-change="change" />升级为管理员&ndash;&gt;-->
<!--        <Button type="info" size="small" @click="remove(index)">升级为管理员</Button>-->
<!--        <Button type="error" size="small" @click="remove(index)">封停</Button>-->
<!--      </template>-->
    </Table>

  </div>

</div>
</template>

<script>
import AdminPage from "../components/adminPage";

export default {
name: "adminUser",
  components: {AdminPage},
  data () {
    return {
      // switch1: false,
      columns: [
        {
          title: '用户头像',
          slot: 'userAvatar',
          align: 'center'
        },
        {
          title: '用户名称',
          slot: 'username',
          align: 'center'
        },
        {
          title: '用户密码',
          slot: 'password',
          align: 'center'
        },
        {
          title: '用户邮箱',
          slot: 'userMail',
          width: '200px',
          align: 'center'
        },
        {
          title: '是否管理员',
          slot: 'userAdmin',
          width:'130px',
          align: 'center',
          filters: [
            {
              label: 'true',
              value: 'true'
            },
            {
              label: 'false',
              value: 'false'
            }
          ],
          filterMethod (value, row) {
            if (value === 'true') {
              return row.userAdmin === true
            } else {
              return row.userAdmin === false
            }
          }
        },
        {
          title: '是否封停',
          slot: 'userStop',
          width:'130px',
          align: 'center',
          filters: [
            {
              label: 'true',
              value: 'true'
            },
            {
              label: 'false',
              value: 'false'
            }
          ],
          filterMethod (value, row) {
            if (value === 'true') {
              return row.userStop === true
            } else {
              return row.userStop === false
            }
          }
        },
        {
          title: '封停/解封',
          slot: 'stop',
          width: 100
        },
        {
          title: '编辑',
          slot: 'action',
          align: 'center',
          width: 160
        },
        {
          title: '操作',
          key: 'action',
          width: 180,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  // size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.show(params.index)
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'error',
                  // size: 'small'
                },
                on: {
                  click: () => {
                    this.remove(params.index)
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data6: [
        {
          name: 'John Brown',
          age: 18,
          address: 'New York No. 1 Lake Park'
        }
      ],
      adminList: [
        {
          value: 'true',
          label: 'true'
        },
        {
          value: 'false',
          label: 'false'
        }
      ],
      showDetail: false,
      userDetail: '',
      userData: [],
      editIndex: -1,  // 当前聚焦的输入框的行数
      editName: '',  // 第一列输入框
      editPassword: '',  // 第二列输入框
      editMail: '',
      editAdmin: '',
      editStop: '',
      loadFlag:true,
      user_stop:'',
    }
  },
  created() {
    let user_data = {
      username: sessionStorage.getItem('username'),
      token: sessionStorage.getItem('token'),
      user_id: sessionStorage.getItem('_id')
    }
    this.$http.post('http://localhost:3000/admin/showUser', user_data).then((data) => {
      this.userData = data.body.data
      this.loadFlag = false
      console.log(data.body)
    })
  },
  methods: {
    change (status){},
    handleStop (row, index) {
      // 从数据库封禁用户
      let update_data;
      if (typeof (sessionStorage.getItem('username')) !== 'undefined') {
        if (sessionStorage.getItem('username') === this.userData[index].username) {
          this.$Modal.error({
            title: '不能封禁自己的账户'
          })
          return
        }
        update_data = {
          username: sessionStorage.getItem('username'),
          token: sessionStorage.getItem('token'),
          user_id: sessionStorage.getItem('_id'),
          stopUser_id: this.userData[index]._id
        }
        console.log(update_data)
        this.$http.post('http://localhost:3000/admin/stopUser', update_data).then((updateData) => {
          if (updateData.body.status === 0) {
            this.$Notice.success({
              title: updateData.body.message,
              duration: 3,
              top: 150
            })
            // 从页面修改评论
            this.userData[index].userStop = true
            this.editIndex = -1
            this.created;
          } else {
            this.$Modal.error({
              title: updateData.body.message
            })
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    },
    handleCancelStop (row, index) {
      // 从数据库解封用户
      let update_data;
      if (typeof (sessionStorage.username) !== 'undefined') {
        update_data = {
          username: sessionStorage.getItem('username'),
          token: sessionStorage.getItem('token'),
          user_id: sessionStorage.getItem('_id'),
          cancelStopUser_id: this.userData[index]._id
        }
        console.log(update_data)
        this.$http.post('http://localhost:3000/admin/cancelStopUser', update_data).then((updateData) => {
          if (updateData.body.status === 0) {
            this.$Notice.success({
              title: updateData.body.message,
              duration: 3,
              top: 150
            })
            // 从页面修改
            this.userData[index].userStop = false
            this.editIndex = -1
            // this.user_stop= 'false'
            this.created;
          } else {
            this.$Modal.error({
              title: updateData.body.message
            })
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    },
    handleEdit (row, index) {
      this.editAdmin = row.userAdmin
      this.editIndex = index
    },
    handleSave (index) {
      // 从数据库修改用户权限
      let update_data;
      if (typeof (sessionStorage.getItem('username')) !== 'undefined') {
        update_data = {
          username: sessionStorage.getItem('username'),
          token: sessionStorage.getItem('token'),
          user_id: sessionStorage.getItem('_id'),
          updateUser_id: this.userData[index]._id,
          userAdmin: this.editAdmin,
        }
        console.log(update_data)
        this.$http.post('http://localhost:3000/admin/updateAdmin', update_data).then((updateData) => {
          if (updateData.body.status === 0) {
            this.$Notice.success({
              title: updateData.body.message,
              duration: 3,
              top: 150
            })
            // 从页面用户修改用户权限
            this.userData[index].userAdmin = this.editAdmin
            // this.userData[index].userPower = this.editPower
            this.editIndex = -1
          } else {
            this.$Modal.error({
              title: updateData.body.message
            })
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    },
    show (index) {
      this.userDetail = ` 用户ID：${this.userData[index]._id}<br> 用户名称：${this.userData[index].username}<br>用户密码：${this.userData[index].password}<br>用户邮箱：${this.userData[index].userMail}<br> 是否管理员：${this.userData[index].userAdmin}<br> 是否封停：${this.userData[index].userStop}`
      this.showDetail = true
      this.$Modal.info({
          title: '用户信息',
          content: this.userDetail
      })

    },
    remove (index) {
      // 从数据库删除用户
      let send_data;
      if (typeof (sessionStorage.username) !== 'undefined') {
        send_data = {
          username: sessionStorage.getItem('username'),
          token: sessionStorage.getItem('token'),
          user_id: sessionStorage.getItem('_id'),
          deleteUser_id: this.userData[index]._id
        }
        this.$Modal.confirm({
          title: '确认删除此条数据吗？',
          onOk: () => {
            this.$http.post('http://localhost:3000/admin/delUser', send_data).then((delData) => {
              if (delData.body.status === 0) {
                this.$Notice.success({
                  title: delData.body.message,
                  duration: 3,
                  top: 150
                })
                // 从页面删除用户
                this.userData.splice(index, 1)
              } else {
                this.$Modal.error({
                  title: delData.body.message
                })
              }
            })
          },
          onCancel: () => {
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    }
  }
}
</script>

<style scoped>

</style>



