<template lang="html">
  <Menu mode="horizontal" :theme="theme1" active-name="1" class="header">
    <router-link to="/">
      <MenuItem name="1" class="header_menu" style="margin-left:35%;margin-right: 30px;">
        <i class="el-icon-house"></i>
        主页
      </MenuItem>
    </router-link>
    <router-link to="/movieList">
      <MenuItem name="2" class="header_menu" style="margin-right: 30px;">
        <i class="el-icon-data-board"></i>
        电影
      </MenuItem>
    </router-link>
    <router-link to="/movieAdd">
      <MenuItem name="3" class="header_menu">
        <i class="el-icon-data-line"></i>
        上传电影
      </MenuItem>
    </router-link>
    <MenuItem name="4" class="header_menu" style="margin-left: 50px">
      <Input placeholder="搜索电影" style="width: auto" v-model="m_name">
        <!--        <router-link :to="{path: '/movieDetail', query:{ id: id }}">-->

        <Icon type="ios-search" slot="suffix" v-on:click="searchMovie"/>
        <!--        </router-link>-->
      </Input>
    </MenuItem>
    <div v-if=!isLogin>
      <div class="demo-avatar">
        <router-link to="/loginPage">
          <!--        <Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" />-->
          <!--        <Avatar style="color: #f56a00;background-color: #fde3cf">U</Avatar>-->
          <Avatar style="background-color: #88B091;" icon="ios-person"/>
        </router-link>
        <Dropdown>
          <router-link to="/loginPage">
            <a href="javascript:void(0)" style="color: #88B091">
              未登录
              <Icon type="ios-arrow-down"></Icon>
            </a>
          </router-link>
          <DropdownMenu slot="list">
            <router-link to="/loginPage">
              <DropdownItem>个人信息</DropdownItem>
              <DropdownItem>忘记密码</DropdownItem>
              <!--          <DropdownItem disabled>豆汁儿</DropdownItem>-->
              <DropdownItem>管理中心</DropdownItem>
              <DropdownItem v-on:click.native="modal1 = true">注销</DropdownItem>
              <!--          <DropdownItem divided>北京烤鸭</DropdownItem>-->
            </router-link>
          </DropdownMenu>
        </Dropdown>

      </div>
    </div>

    <div v-else>
      <div class="demo-avatar">
        <!--        <Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" />-->
        <!--        <Avatar style="color: #f56a00;background-color: #fde3cf">U</Avatar>-->
        <Avatar style="background-color: #88B091;" :src="userAvatar"/>
        <Dropdown>
          <a href="javascript:void(0)" style="color: #88B091">
            已登录
            <Icon type="ios-arrow-down"></Icon>
          </a>
          <DropdownMenu slot="list">
            <DropdownItem v-on:click.native="value1 = true">个人信息</DropdownItem>
            <RouterLink :to="{path: '/changeInfo', query:{ id: id }}">
              <DropdownItem>更改信息</DropdownItem>
              <!--          <DropdownItem disabled>豆汁儿</DropdownItem>-->
            </RouterLink>
            <RouterLink :to="{path: '/admin', query:{ id: id }}">
            <DropdownItem v-if="isAdmin">管理中心</DropdownItem>
            </RouterLink>
            <DropdownItem v-on:click.native="modal1 = true">注销</DropdownItem>
            <!--          <DropdownItem divided>北京烤鸭</DropdownItem>-->
          </DropdownMenu>
        </Dropdown>
      </div>
    </div>
    <Modal
      v-model="modal1"
      title="注销"
      @on-ok="ok"
      @on-cancel="cancel">
      <p>确认注销?</p>
    </Modal>

    <Drawer width="380px" title="个人信息：" :closable="false" v-model="value1">
      <user-info v-bind:id="id"></user-info>
    </Drawer>

  </Menu>
</template>

<script>
import UserInfo from "./userInfo";

export default {
  name: 'MovieIndexHeader',
  components: {UserInfo},
  data() {
    return {
      theme1: 'light',
      movieName: '',
      movieItems: [],
      m_name: '',
      isLogin: false,
      username: '',
      modal1: false,
      userAvatar: '',
      value1: false,
      isAdmin: false,
      id: ''
    }
  },
  mounted() {
    document.querySelector('body').setAttribute('style', 'background-color:#DAEBE3')
  },
  beforeDestroy() {
    document.querySelector('body').removeAttribute('style')
  },
  methods: {
    searchMovie: function () {
      this.$http.post('http://localhost:3000/showName', {
        movieName: this.m_name
      }).then((showMovie) => {
        if (showMovie.body.status === 0) {
          // this.$router.push({path: '/showName/'+this.m_name})
          this.$router.push({path: '/showName', query: {movieName: this.m_name}})
          console.log("aaaaaaaaaaaa" + this.m_name)
        } else {
          this.$Modal.error({
            title: '搜索失败',
            content: showMovie.body.message
          })

        }
      })
    },
    info() {
      this.$Notice.info({title: '未登录'});
    },
    success(username) {
      this.$Notice.success({title: '欢迎登录！' + username});
    },
    logout: function (event) {
      console.log("aaate")
      sessionStorage.clear()
      // this.$router.go(0)
      this.$router.push({path: '/loginPage'})
      this.$Notice.info({
        title: '注销成功'
      })
    },
    ok() {
      this.logout();
    },
    cancel() {
    }
  },
  created() {
    let token = sessionStorage.getItem('token')
    if (token) {  //检测用户登录状态
      this.isLogin = true
      this.username = sessionStorage.getItem('username')
      this.id = sessionStorage.getItem('_id')
      this.$http.post('http://localhost:3000/users/showUser', {user_id: this.id}).then((userData) => {
        this.isAdmin = userData.body.data[0].userAdmin
        this.userAvatar = userData.body.data[0].userAvatar
      })
    } else {
      console.log(this.isLogin)
      this.info()
    }
  }
}
</script>

<style lang="css" scoped>
.header {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  /*border-bottom: 2px solid #88B091;*/
  /*background: #C9E6F0;*/
}

.header_menu {
  left: 30px;
  font-size: 16px;
  /*margin: auto;*/
}

.demo-avatar {
  position: fixed;
  right: 30px;
}
</style>



