<template lang="html">
  <!--      <el-row style="margin: 10px">-->
  <!--        <el-col  :span="8" v-for="(o, index) in 1" :key="o" :offset="index > 0 ? 2 : 0">-->
  <div style="margin:20px 20px 20px 30px;display: inline-block;">
    <el-card :body-style="{ padding: '0px' }" shadow="hover" style="width: 270px;height:400px;border-radius: 10px;">
      <div>
        <router-link :to="{path: '/movieDetail', query:{ id: id }}" class="goods-list-link">
          <img v-bind:src=movieImg class="image">
          <el-divider content-position="left" >{{ movieName }}</el-divider>
          <div class="bottom clearfix">

            <el-button type="text" class="button" style="margin-right: 10px;color: #88B091">
              <Icon type="ios-heart" style="margin-right: 7px"/>
              已有 {{ movieNumSuppose }} 人点赞
            </el-button>
          </div>
        </router-link>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // movieTimeShow: ''
    }
  },
  props: ['id', 'movieName', 'movieImg', 'movieNumSuppose'], /*  props是子组件获取父组件数据用的 */
  created() {
    // this.movieTimeShow = new Date(parseInt(this.movieTime)).toLocaleString().replace(/:\d{1,2}$/, ' ')

  }
}
</script>
<style lang="css" scoped>
.time {
  font-size: 13px;
  color: #999;
}

.el-card:hover {
  margin-top: -5px;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 320px;
  /*display: block;*/
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.moviesList {
  padding: 5px;
  border-bottom: 1px dashed #ababab;
}
</style>



