<template lang="html">
<!--此页面需要-->
  <div class="container">
    <div>
      <movie-index-header ></movie-index-header>   <!--  展示引入的header组件 -->
    </div>

    <div class="contentMain" style="margin-left: 80px">
      <Card style="width: 40%;height:350px;margin: auto;border: #88B091 2px dashed;float: left;padding-bottom: 20px">
      <div style="float: left;border-right: 2px #88B091 dashed;padding-right: 40px;margin-right: 20px">
        <h1>{{detail.movieName}}</h1>

      <div>
        <img class="headerImg" v-bind:src=detail.movieImg>
        <div>
        <Rate show-text allow-half v-model="detail.movieGrade" :disabled="true">
          <span style="color: #f5a623">{{ detail.movieGrade}}</span>
        </Rate>
        </div>
        <div class="viewNum" style="text-decoration: underline;margin: 5px">
          <!--          <Button icon="md-arrow-down" style="margin-left: 20px" v-on:click=movieDownload()></Button>-->
<!--          <Button type="success" ghost style="margin-left: 20px" v-on:click=movieDownload()>下载次数：{{detail.movieNumDownload}}</Button>-->
          <div v-on:click="support()" class="btnPosition">
          </div>
        </div>
        <Button type="error" ghost style="margin-left: 20px" v-on:click=support()>点赞次数：{{detail.movieNumSuppose}}</Button>
        <span v-on:click="movieDownload()" style="margin-left: 10px;text-decoration: underline">
          <span style="cursor: pointer">下载次数：{{detail.movieNumDownload}}</span>
        </span>
      </div>
      </div>
        <div style="padding: 10px;text-align: left">
          <p style="text-align: left">简介：</p>
          <p style="text-indent: 1em">{{ detail.movieInfo }}</p>
        </div>
      </Card>
    </div>
    <div>
      <Card style="width: 50%;margin-right:80px;border: #88B091 2px dashed;height:350px;float:right">
        <video-player class="vjs-custom-skin" :options="playerOptions" ></video-player>
      </Card>
    </div>
    <div style="clear: both;margin-top: 380px;">
    <comment v-bind:movie_id="movie_id"></comment>

</div>

  </div>
</template>
<script>
import MovieIndexHeader from '../components/MovieIndexHeader'
import CommonFooter from '../components/commonFooter'
import Comment from '../components/Comment.vue'
import 'video.js/dist/video-js.css'
import 'vue-video-player/src/custom-theme.css'
//引入hls.js
// import 'videojs-contrib-hls.js/src/videojs.hlsjs'


let movie_id = 0
export default {
  name: 'MovieDetail',
  data () {
    return {
      detail: [],
      movie_id: ' ',
      valueCustomText: 0,
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, //如果true,浏览器准备好时开始回放。
        controls: true, //控制条
        preload: 'auto', //视频预加载
        muted: false, //默认情况下将会消除任何音频。
        loop: false, //导致视频一结束就重新开始。
        language: 'zh-CN',
        aspectRatio: '14:6', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [{
          type: 'video/mp4',
          src:'',//你所放置的视频的地址，最好是放在服务器上
        }],
        poster: "http://39.106.117.192:8080/static/indexImg.png", //你的封面地址（覆盖在视频上面的图片）
        width: document.documentElement.clientWidth,
        notSupportedMessage: '此视频暂无法播放，请稍后再试' //允许覆盖Video.js无法播放媒体源时显示的默认信息。
      }
    }
  },
  components: {
    MovieIndexHeader,
    CommonFooter,
    Comment
  },

  // 获取数据
  created () {
    //    this.$route.query.id
    this.movie_id = this.$route.query.id
    // movie_id = this.$route.query.id
    this.$http.post('http://localhost:3000/movie/detail', {movie_id: this.movie_id}).then((data) => {
      this.detail = data.body.data[0];
      if(this.detail.movieVideo){
        this.playerOptions['sources'][0]['src'] = this.detail.movieVideo
      }else{
        this.playerOptions['sources'][0]['src'] = this.detail.moviePreVideo
      }
      console.log(this.$route.query.id);
      console.log(this.playerOptions['sources'][0]['src']);
    })
  },
  methods: {
    support: function (event) {
      if(!sessionStorage.getItem(this.movie_id)){
        this.$http.post('http://localhost:3000/users/support', {movie_id: this.movie_id}).then((data1) => {
          let data_temp = data1.body
          // let that = this
          console.log(data_temp)
          if (data_temp.status === 0) {
            this.$http.post('http://localhost:3000/users/showNumber', {movie_id: this.movie_id}).then((data2) => {
              console.log(data2.body.data)
              this.detail.movieNumSuppose = data2.body.data.movieNumSuppose
              console.log('点赞测试：'+this.detail.movieNumSuppose+"更新："+data2.body.data.movieNumSuppose)
              sessionStorage.setItem(this.movie_id,'1')
              this.$Notice.info({title:"点赞成功"})
            })
          } else {
            this.$Notice.error({title:data_temp.message})
          }
        })
      }
      else{
        this.$Notice.info({title:"已经点过赞啦！"})
      }

    },
    //    电影下载
    movieDownload: function (event) {
      this.$http.post('http://localhost:3000/movie/download', {movie_id: this.movie_id}).then((data1) => {
        if (data1.status == 1) {
          this.$Notice.info({title:data1.message})
          // alert(data1.message)
        } else {
          this.$http.post('http://localhost:3000/users/showNumber', {movie_id: this.movie_id}).then((data2) => {
            // console.log(data2.body.data)
            this.detail.movieNumDownload = data2.body.data.movieNumDownload
            // console.log('点赞测试：'+this.detail.movieNumSuppose+"更新："+data2.body.data.movieNumSuppose)
          })
          if(data1.data.data != ''){
            window.location = data1.data.data
          }else{
            this.$Message.error("该视频暂未提供下载资源")
          }

        }
      })
    }
  }
}
</script>

<style lang="css" scoped>
  .headerImg{
    height: 200px;
  }
  .container {
    width: 100%;
    margin: 0 auto;
  }
  .contentMain{
    padding-top: 20px;
  }
  /*.btnPosition{*/
  /*  padding-left: 48%;*/
  /*}*/
  .SupportBtn{
    border: solid 1px #000;
    width: 60px;
  }
  .viewNum{
    font-size: 10px;
  }
</style>



