<template>
  <div>
    <div>
      <movie-index-header></movie-index-header>
    </div>
    <div>
      <admin-page></admin-page>
    </div>
    <div style="margin:30px 20px 20px 150px;padding-top: 30px">
      <i-col span="100" offset="1">
        <Table :loading="loadFlag" :columns="columns" :data="movieData" height="700" size="large" ref="table" :border=true>
          <template slot-scope="{ row, index }" slot="movieName">
            <Input type="text" v-model="editName" v-if="editIndex === index" />
            <!--              <span v-else>{{ row.movieName }}</span>-->
            <span v-else>{{ row.movieName.length <= 18 ? row.movieName : row.movieName.substring(0, 18) + '...'}}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieImg">
            <Input type="text" v-model="editImg" v-if="editIndex === index" />
<!--            <span v-else>{{ row.movieImg.length <= 25 ? row.movieImg : row.movieImg.substring(0, 25) + '...'}}</span>-->
            <Img v-else :src="row.movieImg" style="width: 50px;padding: 6px"></Img>
          </template>

          <template slot-scope="{ row, index }" slot="movieInfo">
            <Input type="text" v-model="editMovieInfo" v-if="editIndex === index" />
            <span v-else>{{ row.movieInfo.length <= 25 ? row.movieInfo : row.movieInfo.substring(0, 25) + '...'}}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieVideo">
            <Input type="text" v-model="editVideo" v-if="editIndex === index" />
            <span v-else>{{ row.moviePreVideo.length <= 25 ? row.moviePreVideo : row.moviePreVideo.substring(0, 25) + '...'}}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieDownload">
            <Input type="text" v-model="editDownload" v-if="editIndex === index" />
            <span v-else>{{ row.movieDownload.length <= 25 ? row.movieDownload : row.movieDownload.substring(0, 25) + '...'}}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieTime">
            <Input type="text" v-model="editTime" v-if="editIndex === index" />
            <span v-else>{{ row.movieTime }}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieNumSuppose">
            <Input type="text" v-model="editNumSuppose" v-if="editIndex === index" />
            <span v-else>{{ row.movieNumSuppose }}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieGrade">
            <Input type="text" v-model="editMovieGrade" v-if="editIndex === index" />
            <span v-else>{{ row.movieGrade }}</span>
          </template>

          <template slot-scope="{ row, index }" slot="movieMainPage">
            <Select v-model="editMainPage" style="width:95%" v-if="editIndex === index" >
              <Option v-for="item in mainPageList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            <!--              <Input type="text" v-model="editMainPage" v-if="editIndex === index" />-->
            <span v-else>{{ row.movieMainPage }}</span>
          </template>

          <template slot-scope="{ row, index }" slot="action">
            <div v-if="editIndex === index">
              <Button @click="handleSave(index)" style="color: green;">保存</Button>
              <Button @click="editIndex = -1" style="color: red;">取消</Button>
            </div>
            <div v-else>
              <router-link :to="{path:'/adminMovieDetail',query:{movie_id:row._id}}">
              <Button @click="handleEdit(row, index)">编辑</Button>
              </router-link>
            </div>
          </template>
        </Table>
        <template>
          <Modal v-model="showDetail" fullscreen footer-hide title="详细信息">
            <div align="center">
              <textarea v-model="movieDetail" readonly autofocus rows="30" cols="100" style="font-size: 14px;padding: 10px"></textarea>
            </div>
          </Modal>
        </template>
      </i-col>
    </div>
  </div>
</template>

<script>
import AdminPage from "../components/adminPage";
import MovieIndexHeader from "../components/MovieIndexHeader";

export default {
  name: "movieAdmin",
  components: {MovieIndexHeader, AdminPage},
  data () {
    return {
      loadFlag: true,
      columns: [
        {
          title: '电影名称',
          slot: 'movieName',
          align: 'center'
        },
        {
          title: '图片地址',
          slot: 'movieImg',
          align: 'center'
        },
        {
          title: '电影简介',
          slot: 'movieInfo',
          width: '150px',

        },
        {
          title: '预告片',
          slot: 'movieVideo',
          align: 'center'
        },
        {
          title: '播放地址',
          slot: 'movieDownload'
        },
        {
          title: '上映时间',
          slot: 'movieTime',
          align: 'center'
        },
        {
          title: '点赞数',
          slot: 'movieNumSuppose',
          align: 'center'
        },
        {
          title: '电影分数',
          slot: 'movieGrade',
          align: 'center'
        },
        {
          title: '主页',
          slot: 'movieMainPage',
          align: 'center',
          filters: [
            {
              label: 'true',
              value: 'true'
            },
            {
              label: 'false',
              value: 'false'
            }
          ],
          filterMethod (value, row) {
            if (value === 'true') {
              return row.movieMainPage === true
            } else {
              return row.movieMainPage === false
            }
          }
        },
        {
          title: '编辑',
          slot: 'action',
          width: 160,
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 140,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.show(params.index)
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.remove(params.index)
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      mainPageList: [
        {
          value: 'true',
          label: 'true'
        },
        {
          value: 'false',
          label: 'false'
        }
      ],
      showDetail: false,
      movieDetail: '',
      movieData: [],
      editIndex: -1,  // 当前聚焦的输入框的行数
      editName: '',  // 第一列输入框，
      editImg: '',  // 第二列输入框
      editVideo: '',  // 第三列输入框
      editDownload: '',
      editTime: '',
      editNumSuppose: '',
      editMovieGrade: '',
      editMainPage: '',
      editMovieInfo:''
    }
  },
  created () {
    // 获取电影列表
    this.$http.get('http://localhost:3000/admin/movie').then((data) => {
      this.movieData = data.body.data
      this.loadFlag = false
      console.log(data.body.data)
    })
  },
  methods: {
    handleEdit (row, index) {
      this.editId = row._id
      this.editName = row.movieName
      this.editImg = row.movieImg
      this.editVideo = row.movieVideo
      this.editDownload = row.movieDownload
      this.editTime = row.movieTime
      // this.editNumSuppose = row.movieNumSuppose
      this.editMovieGrade=row.movieGrade
      this.editNumDownload = row.movieNumDownload
      this.editMainPage = row.movieMainPage
      this.editIndex = index
      this.editMovieInfo=row.movieInfo
    },
    handleSave (index) {
      // 从数据库修改电影
      let update_data;
      if (typeof (sessionStorage.username) !== 'undefined') {
        update_data = {
          username: sessionStorage.username,
          token: sessionStorage.token,
          user_id: sessionStorage._id,
          movie_id: this.editId,
          movieName: this.editName,
          movieImg: this.editImg,
          movieVideo: this.editVideo,
          movieDownload: this.editDownload,
          movieTime: this.editTime,
          movieNumSuppose: this.editNumSuppose,
          movieGrade: this.editMovieGrade,
          movieMainPage: this.editMainPage,
          movieInfo:this.editMovieInfo
        }
        console.log(update_data)
        this.$http.post('http://localhost:3000/admin/movieUpdate', {movieData:update_data}).then((updateData) => {
          if (updateData.body.status === 0) {
            this.$Notice.success({
              title: updateData.body.message,
              duration: 3,
              top: 150
            })
            // this.$Modal.success({
            //   title: updateData.body.message
            // })
            // 从页面修改电影
            this.movieData[index].movieName = this.editName
            this.movieData[index].movieImg = this.editImg
            this.movieData[index].movieVideo = this.editVideo
            this.movieData[index].movieDownload = this.editDownload
            this.movieData[index].movieTime = this.editTime
            this.movieData[index].movieNumSuppose = this.editNumSuppose
            this.movieData[index].movieNumDownload = this.editNumDownload
            this.movieData[index].movieMainPage = this.editMainPage
            this.movieData[index].movieInfo = this.editMovieInfo
            this.movieData[index].movieGrade = this.editMovieGrade
            this.editIndex = -1
          } else {
            this.$Modal.error({
              title: updateData.body.message
            })
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    },
    show (index) {
      this.movieDetail = ` 电影ID：${this.movieData[index]._id}\n\n 电影名称：${this.movieData[index].movieName}\n\n 图片地址：${this.movieData[index].movieImg}\n\n 预告片：${this.movieData[index].moviePreVideo}\n\n 观看地址：${this.movieData[index].movieDownload}\n\n 上映时间：${this.movieData[index].movieTime}\n\n 点赞数量：${this.movieData[index].movieNumSuppose}\n\n 观看数量：${this.movieData[index].movieNumDownload}\n\n 主页显示：${this.movieData[index].movieMainPage}\n\n 电影简介：${this.movieData[index].movieInfo}`
      // this.movieDetail = ` 电影ID：${this.movieData[index]._id}<br> 电影名称：${this.movieData[index].movieName}<br> 图片地址：${this.movieData[index].movieImg}<br> 预告片：${this.movieData[index].moviePreVideo}<br> 观看地址：${this.movieData[index].movieDownload}<br> 上映时间：${this.movieData[index].movieTime}<br> 点赞数量：${this.movieData[index].movieNumSuppose}<br> 观看数量：${this.movieData[index].movieNumDownload}<br> 主页显示：${this.movieData[index].movieMainPage}`
      this.showDetail = true
      // this.$Modal.info({
      //   title: '用户信息',
      //   content: this.movieDetail
      // })
    },
    remove (index) {
      // 从数据库删除电影
      let send_data;
      if (typeof (sessionStorage.username) !== 'undefined') {
        send_data = {
          username: sessionStorage.username,
          token: sessionStorage.token,
          user_id: sessionStorage._id,
          movie_id: this.movieData[index]._id
        }
        this.$Modal.confirm({
          title: '确认删除此条数据吗？',
          onOk: () => {
            this.$http.post('http://localhost:3000/admin/movieDel', send_data).then((delData) => {
              if (delData.body.status === 0) {
                this.$Notice.success({
                  title: delData.body.message,
                  duration: 3,
                  top: 150
                })
              } else {
                this.$Modal.error({
                  title: delData.body.message
                })
              }
            })
            // 从页面删除电影
            this.movieData.splice(index, 1)
          },
          onCancel: () => {
          }
        })
      } else {
        this.$Modal.error({
          title: '请先登录'
        })
      }
    }
  }
}
</script>

<style scoped>

</style>



