<template lang="html">
  <div>
    <div class="container">
      <movie-index-header></movie-index-header>
    </div>
    <div class="bbox"></div>
    <div class="box">
      <div style="width: 15%;padding-top: 8%">
        <h2 style="color: #88B091;font-size: 25px;margin-left: auto">Change Info</h2>
        <div class="demo-avatar" style="margin: 20px;">
          <Img shape="square" :src="userAvatar" style="border: #88B091 2px dotted;width: 80px;border-radius: 5px"/>
        </div>

        <div class="box">
          <Input v-model="username" size="large" placeholder="请输入用户名" :value="username"/>
        </div>
        <div class="box" style="margin: auto auto">
          <Upload
            class="avatar-uploader"
            :show-upload-list ="false"
            :data="qiniuForm"
            :before-upload="beforeUpload"
            :on-success="uploadSuccess"
            :limit="1"
            action="https://up-z2.qiniup.com">
            <!--            :on-exceed="handleExceed"-->
            <!--            :on-remove="handleRemove"-->
            <Button style="margin-top: 3px" icon="ios-cloud-upload-outline">上传新头像</Button>
          </Upload>
          <i-button type="success" v-on:click=change()
                    style="margin-left: 10px;background-color: #88B091;border: #88B091;font-size: larger">
            完成修改
          </i-button>
          <!--      <button v-on:click=userRegister()>注册</button>-->
        </div>

      </div>
    </div>
  </div>
</template>

<script>

import MovieIndexHeader from '../components/MovieIndexHeader'

export default {
  name: 'changeInfo',
  components: {MovieIndexHeader},
  data() {
    return {
      username: '',
      userAvatar: '',
      imageUrl: this.userAvatar,
      // token: {},
      // 七牛云的上传地址，根据自己所在地区选择，我这里是华南区
      domain: 'https://up-z2.qiniup.com',
      // 这是七牛云空间的外链默认域名
      qiniuaddr: 'qmaqpkabs.hn-bkt.clouddn.com',
      qiniuForm:{
        key:'',
        token:''
      }
    }
  },
  created() {
    let userId = this.$route.query.id
    console.log(userId + "ttt")
    if (userId) {
      this.$http.post('http://localhost:3000/users/showUser', {user_id: userId}).then((getUserInfo) => {
        if (getUserInfo.body.status === 0) {
          this.username = getUserInfo.body.data[0].username
          this.userAvatar = getUserInfo.body.data[0].userAvatar
          this.imageUrl = this.userAvatar
        } else {
          console.log(getUserInfo.body);
          // alert(userData.body.message)
          this.$Notice.info({title: getUserInfo.body.message});
        }
      })
    } else {
      this.$Notice.info({title:'请先登录'});
    }
  },
  methods: {
    change: function (event) {
      let userId = this.$route.query.id
      if (userId) {
        this.$http.post('http://localhost:3000/users/changeInfo', {user_id: userId,username:this.username,userAvatar:this.imageUrl}).then((getUserInfo) => {
          if (getUserInfo.body.status === 0) {
            console.log(getUserInfo.body);
            if (userId) {
              this.$http.post('http://localhost:3000/users/showUser', {user_id: userId}).then((getUserInfo) => {
                if (getUserInfo.body.status === 0) {
                  console.log("更新"+getUserInfo.body);
                  this.username = getUserInfo.body.data[0].username
                  this.userAvatar = getUserInfo.body.data[0].userAvatar
                  this.$Notice.success({title: "修改成功"});
                } else {
                  console.log(getUserInfo.body);
                  // alert(userData.body.message)
                  this.$Notice.info({title: getUserInfo.body.message});
                }
              })
            } else {
              this.$Notice.info({title:'请先登录'});
            }
          } else {
            console.log(getUserInfo.body);
            // alert(userData.body.message)
            this.$Notice.info({title: getUserInfo.body.message});
          }
        })
      } else {
        this.$Notice.info({title:'请先填写用户名'});
      }
    },
  //   upqiniu(req) {
  //     console.log(req)
  //     const config = {
  //       headers: {'Content-Type': 'multipart/form-data'}
  //     }
  //     let filetype = ''
  //     if (req.file.type === 'image/png') {
  //       filetype = 'png'
  //     } else {
  //       filetype = 'jpg'
  //     }
  //     // 重命名要上传的文件
  //     const keyname = 'whh' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + filetype
  //     // 从后端获取上传凭证token
  //     console.log(keyname+"cesgu测试")
  //     this.axios.get('/upload/token').then(res => {
  //       // res.header('Access-Control-Allow-Origin', '*');
  //       console.log(res)
  //       const formdata = new FormData()
  //       formdata.append('file', req.file)
  //       formdata.append('token', res.data)
  //       formdata.append('key', keyname)
  //       // 获取到凭证之后再将文件上传到七牛云空间
  //       this.axios.post(this.domain, formdata, config).then(res => {
  //         this.userAvatar = 'http://' + this.qiniuaddr + '/' + res.data.key
  //         console.log(this.userAvatar)
  //       })
  //     }) .catch(function (error) {
  //       console.log(res+"aaaaaaaaaaaaa")
  //       if (error.response) {
  //         console.log(error.response)
  //       } else if (error.request) {
  //         console.log(error.request);
  //       } else {
  //         // Something happened in setting up the request that triggered an Error
  //         console.log('Error', error.message);
  //       }
  //       console.log(error.config);
  //     });
  //   },
  // // 验证文件合法性
  // beforeUpload (file) {
  //   const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
  //   const isLt2M = file.size / 1024 / 1024 < 2
  //   if (!isJPG) {
  //     this.$message.error('上传头像图片只能是 JPG/PNG 格式!')
  //   }
  //   if (!isLt2M) {
  //     this.$message.error('上传头像图片大小不能超过 2MB!')
  //   }
  //   return isJPG && isLt2M
  // }
    async beforeUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG或PNG 格式!')
      }

      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }

      //如果验证失败，返回假
      if(isJPG && isLt2M == false) return false;

      //如果验证成功，获取token
      let fileType = file.type == 'image/jpeg' ? 'jpg' : 'png'; //获取扩展名
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key:'avatar_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
        token: res.data.data.token  // 根据自己接口打印的值来赋值
      }
      this.$Notice.info({title:'正在上传头像'});
      return true;
    },

    /**
     * 上传成功之后得到image
     * @param res
     * @param file
     * @param fileList
     */
    uploadSuccess(res,file,fileList) {
      //console.log(res)
      this.imageUrl='http://qmaqpkabs.hn-bkt.clouddn.com/'+res.key
      console.log(this.imageUrl)
      this.$Notice.info({title:'上传完成'});
      // let url=this.imageUrl
      this.change()
    },

  }
}
</script>

<style lang="css" scoped>
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.bbox {
  position: fixed;
  border: 2px solid #88B091;
  border-radius: 10px;
  top: 20%;
  left: 40%;
  width: 320px;
  height: 350px;
  background: white;
  z-index: -1;
}

i-input {
  line-height: 40px;
}
</style>



