<template>
  <div>
    <div>
      <movie-index-header></movie-index-header>
    </div>
    <div>
      <admin-page></admin-page>
    </div>
    <div>
      <Form ref="formItem" :rules="ruleValidate" :model="detail" :label-width="80"
            style="text-align:left;margin:auto;width:500px;padding-top: 30px">
        <!--    <h2 style="text-align: center;padding: 10px">添加电影</h2>-->
        <FormItem label="电影名称" prop="movieName">
          <Input v-model="detail.movieName" placeholder="请输入电影名称"></Input>
        </FormItem>
        <FormItem label="电影图片" prop="movieImg">
          <Input v-model="detail.movieImg" placeholder="请输入电影图片地址"></Input>
          <Upload
            type="drag"
            style="margin-top: 10px;"
            :data="qiniuForm"
            :before-upload="beforeUpload"
            :on-success="uploadSuccess"
            :limit="1"
            action="https://up-z2.qiniup.com">
            <div style="padding: 20px 0">
              <Icon type="ios-cloud-upload" size="52" style="color:#88B091"></Icon>
              <p>上传新图片</p>
            </div>
          </Upload>

        </FormItem>
        <FormItem label="电影简介" prop="movieInfo">
          <Input v-model="detail.movieInfo" type="textarea" :autosize="{minRows: 2,maxRows: 30}"
                 placeholder="请输入电影简介"></Input>
        </FormItem>
        <FormItem label="电影类别" prop="movieTag">
          <Select v-model="detail.movieTag">
            <Option value="1">动画</Option>
            <Option value="0">冒险</Option>
            <Option value="2">剧情</Option>
          </Select>
        </FormItem>
        <FormItem label="电影分数" prop="movieGrade">
          <Rate show-text allow-half v-model="detail.movieGrade">
            <span style="color: #f5a623">{{ detail.movieGrade }}</span>
          </Rate>
        </FormItem>
        <FormItem label="主页电影">
          <i-switch v-model="detail.movieMainPage" size="large">
            <span slot="open">On</span>
            <span slot="close">Off</span>
          </i-switch>
        </FormItem>

          <FormItem label="电影视频" prop="movieVideo">
            <Upload
              type="drag"
              style="margin-top: 10px;"
              :data="qiniuForm"
              :before-upload="beforeUploadMovie"
              :on-success="uploadSuccessMovie"
              accept=".mp4"
              :limit="1"
              action="https://up-z2.qiniup.com">
              <div style="padding: 20px 0">
                <Icon type="ios-cloud-upload" size="52" style="color:#88B091"></Icon>
                <p>上传电影</p>
              </div>
            </Upload>

            <Input v-model="detail.movieVideo" placeholder="请输入电影视频播放地址"></Input>
          </FormItem>
          <FormItem label="电影下载" prop="movieDownload">
            <Input v-model="detail.movieDownload" placeholder="请输入电影下载地址"></Input>
          </FormItem>
          <FormItem label="电影预告片" prop="moviePreVideo">
            <Input v-model="detail.moviePreVideo" placeholder="请输入电影预告片播放地址"></Input>
          </FormItem>
          <FormItem label="上映时间" prop="movieTime">
            <Input v-model="detail.movieTime" placeholder="请输入电影上映时间"></Input>
          </FormItem>
        <FormItem style="text-align: center">
          <Button type="primary" @click="handleSubmit('formItem')">保存修改</Button>
          <Button @click="handleReset('formItem')" style="margin-left: 8px">清空</Button>
        </FormItem>
      </Form>

    </div>
  </div>
</template>

<script>
import MovieIndexHeader from "../components/MovieIndexHeader";
import AdminPage from "../components/adminPage";
export default {
  name: "adminMovieDetail",
  components: {AdminPage, MovieIndexHeader},
  data(){
    return{
      detail:[],
      ruleValidate: {
        movieName: [
          {required: true, message: '电影名称不能为空', trigger: 'blur'}
        ],
        movieImg: [
          {required: true, message: '电影图片不能为空', trigger: 'blur'},
        ],
        movieInfo: [
          {required: true, message: '电影简介不能为空', trigger: 'blue'}
        ],
      },
      domain: 'https://up-z2.qiniup.com',
      // 这是七牛云空间的外链默认域名
      qiniuaddr: 'qmaqpkabs.hn-bkt.clouddn.com',
      qiniuForm:{
        key:'',
        token:''
      }

    }
  },
  created() {
    console.log(this.$route.query.movie_id)
    this.$http.post('http://localhost:3000/movie/detail', {movie_id: this.$route.query.movie_id}).then((data) => {
      this.detail = data.body.data[0];
    })
  },
  methods:{
    handleSubmit(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('校验成功!')
          let update_data;
          if (typeof (sessionStorage.username) !== 'undefined') {
            update_data = {
              username: sessionStorage.username,
              token: sessionStorage.token,
              user_id: sessionStorage._id,
              movie_id: this.detail._id,
              movieName: this.detail.movieName,
              movieImg: this.detail.movieImg,
              movieVideo: this.detail.movieVideo,
              movieDownload: this.detail.movieDownload,
              movieTime: this.detail.movieTime,
              movieNumSuppose: this.detail.movieNumSuppose,
              movieGrade: this.detail.movieGrade,
              movieMainPage: this.detail.movieMainPage,
              movieInfo:this.detail.movieInfo
            }
            console.log(update_data)
            this.$http.post('http://localhost:3000/admin/movieUpdate', {movieData:update_data}).then((updateData) => {
              if (updateData.body.status === 0) {
                this.$Notice.success({
                  title: updateData.body.message,
                  duration: 3,
                  top: 150
                })

              } else {
                this.$Modal.error({
                  title: updateData.body.message
                })
              }
            })
          } else {
            this.$Modal.error({
              title: '请先登录'
            })
          }

        } else {
          this.$Message.error('Fail!');
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    async beforeUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      // const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG或PNG 格式!')
      }

      //如果验证失败，返回假
      if(isJPG == false) return false;

      //如果验证成功，获取token
      let fileType = file.type == 'image/jpeg' ? 'jpg' : 'png'; //获取扩展名
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key:'movieImg_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
        token: res.data.data.token  // 根据自己接口打印的值来赋值
      }
      this.$Notice.info({title:'正在上传图片'});
      return true;
    },

    /**
     * 上传成功之后得到image
     * @param res
     * @param file
     * @param fileList
     */
    uploadSuccess(res,file,fileList) {
      //console.log(res)
      this.detail.movieImg='http://qmaqpkabs.hn-bkt.clouddn.com/'+res.key
      this.$Notice.info({title:'上传完成'});
      // let url=this.imageUrl
    },

    async beforeUploadMovie(file) {
      console.log("ssssccccc")
      const isMP4 = file.type === 'video/mp4'

      if (!isMP4) {
        this.$message.error('上传视频暂只支持 MP4 格式!')
      }

      //如果验证失败，返回假
      if(isMP4 == false) return false;

      //如果验证成功，获取token
      let fileType = 'mp4'; //获取扩展名
      let res = await this.axios.get('http://127.0.0.1:3000/upload/token')
      console.log(res);
      console.log(res.data.data.token)
      // return;
      this.qiniuForm = {
        key:'movieVideo_test_' + new Date().getTime() + Math.floor(Math.random() * 100) + '.' + fileType, //文件名称
        token: res.data.data.token  // 根据自己接口打印的值来赋值
      }
      this.$Notice.info({title:'正在上传图片'});
      return true;
    },

    /**
     * 上传成功之后得到image
     * @param res
     * @param file
     * @param fileList
     */
    uploadSuccessMovie(res,file,fileList) {
      //console.log(res)
      this.detail.movieVideo='http://qmaqpkabs.hn-bkt.clouddn.com/'+res.key
      this.$Notice.info({title:'上传完成'});
      // let url=this.imageUrl
    }
  }
}
</script>

<style scoped>

</style>



